import produce from "immer"
import API from 'api'

const LOGIN = 'LOGIN'
const LOGIN_PENDING = 'LOGIN_PENDING'
const LOGIN_SUCCESS = 'LOGIN_SUCCESS'
const LOGIN_FAILURE = 'LOGIN_FAILURE'
const LOGOUT = 'LOGOUT'
const REGISTER = 'REGISTER'
const REGISTER_PENDING = 'REGISTER_PENDING'
const REGISTER_SUCCESS = 'REGISTER_SUCCESS'
const REGISTER_FAILURE = 'REGISTER_FAILURE'

export const login = (userid, password) => ({
  type: LOGIN,
  payload: API.Auth.login(userid, password)
})

export const logout = () => ({
    type: LOGOUT
})

export const register = (username, email, password) => ({
  type: REGISTER,
  payload: API.Auth.register(username, email, password)
})

const initialState = {
  user: localStorage.getItem('jwt') || null,
  loading: false,
  error: [],
}

export default (state = initialState, action) => {
  return produce(state, draft => {
    switch (action.type) {
      case LOGIN_PENDING:
      case REGISTER_PENDING:
        draft.loading = true
        return
      
      case LOGIN_SUCCESS:
      case REGISTER_SUCCESS:
        const { token } = action.payload.data.user
        API.setToken(token)
        draft.user = token
        draft.loading = false
        draft.error = []
        return

      case LOGIN_FAILURE:
      case REGISTER_FAILURE:
        const errors = action.payload.response.data.errors
        const msg = Object.keys(errors).map(k => {
          return `${k} ${errors[k].join(', ')}`
        })

        draft.loading = false
        draft.error = errors
        return

      case LOGOUT:
        API.setToken(null)
        draft.user = false
        return

      default:
        return state
    }
  })
}