import React, { Component } from 'react'
import API from 'api'

class Home extends Component {
  state = {
    articlesCount: 0,
    articles: []
  }

  componentDidMount() {
    this.fetchArticles()
  }
  
  fetchArticles = async () => {
    const { data } = await API.Articles.all()
    const { articlesCount, articles } = data
    
    this.setState(() => ({
     articlesCount,
     articles
    }))
  }

  testCreate = () => {
    API.Articles.create({
      title: new Date(),
      description: 'aaaa',
      body: 'aaaasf',
      tagList: [],
    }).then(data => {
      console.log(data)
    })
  }

  render() {
    const { articlesCount, articles } = this.state

    return (
      <>
        <h2>Home</h2>

        <div>
          <button type="button" onClick={this.testCreate}>테스트 글쓰기(로그인 필요)</button>
        </div>

        <p>total : {articlesCount}</p>

        <ul>
          {
            articles.map(v => (
              <li key={v.slug}>
                <p className="title">제목 : {v.title}</p>
                <p className="desc">{v.description}</p>
                <div className="info">
                  <span className="author">작성자: {v.author.username}</span>{' '}
                  <span className="date">작성일: {v.updatedAt}</span>{' '}
                  <span className="tag">Tag: {v.tagList.join(', ')}</span>
                </div>
              </li>
            ))
          }
        </ul>
      </>
    )
  }
}

export default Home