import React, { Component } from 'react'
import { Redirect } from 'react-router-dom';
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import * as authActions from 'store/modules/auth'

class Join extends Component {
  state = {
    username: '',
    email: '',
    password: ''
  }

  submitHandle = (e) => {
    const { authActions } = this.props
    const { username, email, password } = this.state

    authActions.register(username, email, password)

    e.preventDefault()
  }

  changeInput = name => e => {
    const { value } = e.target
    this.setState(() => ({
      [name]: value
    }))
  }

  render() {
    const { user, loading, error } = this.props
    const { username, email, password } = this.state

    return user ?
      <Redirect to="/" />
      :
      (
        <>
          <h2>회원가입</h2>
          <form onSubmit={this.submitHandle}>
            <fieldset>
              <div>
                <label>
                  username
                  <input type="text" disabled={loading} value={username} onChange={this.changeInput('username')} />
                </label>
              </div>
              <div>
                <label>
                  email
                  <input type="email" disabled={loading} value={email} onChange={this.changeInput('email')} />
                </label>
              </div>
              <div>
                <label>
                  password
                  <input type="password" disabled={loading} value={password} onChange={this.changeInput('password')} />
                </label>
              </div>
              { loading && <div>회원가입 중...</div> }
              { error.length > 1 && error.map(err => <div key={err}>{err}</div>) }
              <button type="submit" disabled={loading}>회원가입</button>{' '}
              <button type="button" disabled={loading}>취소</button>
            </fieldset>
          </form>
        </>
      )
  }
}

const mapStateToProps = (state) => ({
  user: state.auth.user,
  loading: state.auth.loading,
  error: state.auth.error,
})

const mapDispatchToProps = (dispatch) => ({
  authActions: bindActionCreators(authActions, dispatch)
})

export default connect(mapStateToProps, mapDispatchToProps)(Join)