import http from 'axios'

const API_ROOT = 'https://conduit.productionready.io/api'

// const encode = encodeURIComponent
// const responseBody = res => res.data

const setToken = (token = null) => {
  if(token) {
    window.localStorage.setItem('jwt', token)
    http.defaults.headers.common['Authorization'] = `Token ${token}`
  } else {
    window.localStorage.removeItem('jwt')
    delete http.defaults.headers.common['Authorization']
  }
}

const req = {
  del: url =>
    http.del(`${API_ROOT}${url}`),
  get: url =>
    http.get(`${API_ROOT}${url}`),
  put: (url, body) =>
    http.put(`${API_ROOT}${url}`, body),
  post: (url, body) =>
    http.post(`${API_ROOT}${url}`, body),
}

const Auth = {
  current: () =>
    req.get('/user'),
  login: (email, password) =>
    req.post('/users/login', { user: { email, password } }),
  register: (username, email, password) =>
    req.post('/users', { user: { username, email, password } }),
  save: user =>
    req.put('/user', { user })
}

const limit = (count, page) => `limit=${count}&offset=${page ? page * count : 0}`
const Articles = {
  all: page =>
    req.get(`/articles?${limit(10, page)}`),
  create: article =>
    req.post('/articles', { article }),
}

const Profile = {
  follow: username =>
    req.post(`/profiles/${username}/follow`),
  get: username =>
    req.get(`/profiles/${username}`),
  unfollow: username =>
    req.del(`/profiles/${username}/follow`)
}

export default {
  setToken,
  Auth,
	Articles,
  Profile,
}